.container {
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - 3.5em);
    justify-content: center;
    align-items: center;
    text-align: center;
    background-position: center !important;
    background-size: cover !important;
}

.container > div {
    padding: 0.75em 0.25em;
}

.professionalHeadshot {
    height: 10em;
    /* width: 9em; */
    border-radius: 50%;
}

.textWhite {
    color: white !important;
}

.headingOne {
    font-size: 2.5em;
}

.headingThree {
    font-size: 1.5em;
}

.icon {
    color: white;
    font-size: 2.5em;
    border-radius: 0.125em;
}

.icon:hover {
    color: #404040;
    background-color: white;
}

.iconSmall {
    font-size: 1.25em !important;
}

#skills{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}